@import url(https://fonts.googleapis.com/css2?family=Goldman&display=swap);
.App {
  text-align: center;
  font-family: "Goldman", cursive;
}

/* normalize */
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}
a {
  color: #f48fb1;
  text-decoration: none;
}
a:hover {
  color: #ff6095;
}

.QRreader_QRreader__2o0nq {
  color: rgb(49, 49, 49);
