}

.Login_loginCard__3Al-t {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 35%;
  min-height: 250px;
  margin: 15px 15px;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0px 0px 10px 3px #13aa527e;
